import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators, ReactiveFormsModule } from '@angular/forms';
import { SharedService } from '../../shared/services/shared.service';
import { HfButtonComponent, HfInputComponent, HfCardComponent, HfCheckboxComponent, inputMsgType, HfModelComponent, HfToastService, PhoneNumberFormatterDirective } from '@monorepo/ui';
import { AppService, AuthService, CheckEmailInfo, LoginInfo, LoginResponse, StoreService, compareValidator, maxLengthRestriction } from '@monorepo/utils';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { CustomerTaxType } from '../../shared/models/enums';
// eslint-disable-next-line @nx/enforce-module-boundaries
import { AuthComponent } from '@monorepo/auth';
import { Subscription } from 'rxjs';
import { OrderDetailsBarComponent } from '../../shared/components/order-details-bar/order-details-bar.component';
import { Router } from '@angular/router';
import { URLS } from '../../shared/utils/urls';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'app-order-login',
  standalone: true,
  templateUrl: './order-login.component.html',
  styleUrl: './order-login.component.scss',
  imports: [
    CommonModule,
    ReactiveFormsModule,
    OrderDetailsBarComponent,
    HfButtonComponent,
    HfInputComponent,
    HfInputComponent,
    HfCardComponent,
    HfCheckboxComponent,
    HfModelComponent,
    AuthComponent,
    PhoneNumberFormatterDirective
  ]
})

export class OrderLoginComponent implements OnInit, OnDestroy {

  showSignInModal: boolean = false;
  showTaxExemptModel: boolean = false;
  callRegisterCustomerApiSubscription: Subscription;
  emailInfo: Subscription;
  loginApiSubscription: Subscription;
  loginInfo: Subscription;
  orderParams: object;
  encryptedEmail: string = '';
  encryptedPassword: string = '';
  fileUrl: string;
  nextButtonDisable: boolean = false;
  nextButtonText: string = 'NEXT';

  orderLoginForm: FormGroup = new FormGroup({
    firstName: new FormControl(null, [Validators.required]),
    lastName: new FormControl(null, [Validators.required]),
    residence: new FormControl(),
    taxExempt: new FormControl(),
    base64: new FormControl(),
    address: new FormControl(null, [Validators.required]),
    email: new FormControl(null, [
      Validators.required,
      Validators.pattern(this.sharedService.emailKey),
    ]),
    confirmEmail: new FormControl(null),
    phoneNumber: new FormControl(null, [
      Validators.required,
      maxLengthRestriction(14),
      Validators.pattern(this.sharedService.phoneNumberKey)
    ]),
    password: new FormControl(''),
    confirmPassword: new FormControl(''),
  }, { validators: [compareValidator('email', 'confirmEmail', 'emailNotMatch')] });

  get base64() {
    return this.orderLoginForm.get('base64') as FormControl
  }
  get firstName() {
    return this.orderLoginForm.get('firstName') as FormControl
  }
  get lastName() {
    return this.orderLoginForm.get('lastName') as FormControl
  }
  get residence() {
    return this.orderLoginForm.get('residence') as FormControl
  }
  get taxExempt() {
    return this.orderLoginForm.get('taxExempt') as FormControl
  }
  get address() {
    return this.orderLoginForm.get('address') as FormControl
  }
  get email() {
    return this.orderLoginForm.get('email') as FormControl
  }
  get confirmEmail() {
    return this.orderLoginForm.get('confirmEmail') as FormControl
  }
  get phoneNumber() {
    return this.orderLoginForm.get('phoneNumber') as FormControl
  }
  get password() {
    return this.orderLoginForm.get('password') as FormControl
  }
  get confirmPassword() {
    return this.orderLoginForm.get('confirmPassword') as FormControl
  }

  firstNameIM: string = '';
  firstNameMT: inputMsgType = '';
  lastNameIM: string = '';
  lastNameMT: inputMsgType = '';
  addressIM: string = '';
  addressMT: inputMsgType = '';
  emailIM: string = '';
  emailMT: inputMsgType = '';
  confirmEmailIM: string = '';
  confirmEmailMT: inputMsgType = '';
  phoneNumberIM: string = '';
  phoneNumberMT: inputMsgType = '';
  passwordIM: string = '';
  passwordMT: inputMsgType = '';
  confirmPasswordIM: string = '';
  confirmPasswordMT: inputMsgType = '';

  constructor(
    private sharedService: SharedService,
    private http: HttpClient,
    public storeService: StoreService,
    private toastService: HfToastService,
    public authService: AuthService,
    private router: Router,
    public appService: AppService,
  ) { }

  ngOnInit() {
    this.residence.setValue(true);
    if (this.appService.isBrowserRendering) {
      this.storeService.getValueFromStore('orderParams', (orderParams) => {
        this.orderParams = orderParams;
      });
    }
  }

  checkIfEmailAlreadyExist() {
    this.authService.checkEmailInfo.next({ email: this.email.value, isVaild: false });
    this.emailInfo = this.authService.checkEmailInfo$.subscribe((res: CheckEmailInfo) => {
      if (res.isVaild) {
        this.showSignInModal = true;
        this.getLoginInfo();
      }
    });
  }

  getLoginInfo() {
    this.loginInfo = this.authService.isLoginDone$.subscribe((res: LoginInfo) => {
      if (res.isLoginDone) {
        if (!this.appService.isApp) {
          if (!environment.is_local) {
            this.authService.processLoginResponse(res.loginResponse, '.heatfleet.com');
          } else {
            this.authService.processLoginResponse(res.loginResponse);
          }
        } else {
          const expirationDate = new Date(Date.now() + 7 * 24 * 60 * 60 * 1000); // Expires in 7 days
          const expires = expirationDate.toUTCString();
          this.authService.processLoginResponse(res.loginResponse, 'localhost', '/', expires);
        }
        this.toastService.toastMessage.next({ title: 'Success', msg: 'Login successful', type: 'success' });
        this.authService.isLoginDone.next({ isLoginDone: false, loginResponse: null });
        this.closeSignInModal(true);
        this.router.navigate([URLS.CONFIRM_ADDRESS]);
      }
    });
  }

  openSignInModal() {
    this.showSignInModal = true;
    this.getLoginInfo();
  }

  closeSignInModal(isClosed: boolean) {
    if (isClosed) {
      this.showSignInModal = false;
      if (this.emailInfo) {
        this.emailInfo.unsubscribe();
      }
      if (this.loginInfo) {
        this.loginInfo.unsubscribe();
      }
    }
  }

  validateOrderLoginForm() {
    if (this.password.value != '') {
      this.password.setValidators([Validators.required, Validators.minLength(8), Validators.pattern(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/)]);
      this.orderLoginForm.addValidators([compareValidator('password', 'confirmPassword', 'passwordNotMatch')]);
    } else {
      this.passwordMT = '';
      this.passwordIM = '';
      this.confirmPasswordMT = '';
      this.confirmPasswordIM = '';
      this.password.clearValidators();
      this.orderLoginForm.clearValidators();
      this.orderLoginForm.addValidators([compareValidator('email', 'confirmEmail', 'emailNotMatch')]);
    }
    this.password.updateValueAndValidity();
    this.orderLoginForm.updateValueAndValidity();
    if (this.orderLoginForm.invalid) {
      this.nextButtonDisable = false;
      this.nextButtonText = 'NEXT';
      if (this.firstName.invalid) {
        this.firstNameMT = "error";
        if (this.firstName?.errors['required']) {
          this.firstNameIM = "First name is required";
        }
        const validFirstName = this.firstName.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.firstNameIM = '';
            this.firstNameMT = '';
            validFirstName.unsubscribe();
          }
        });
      }
      if (this.lastName.invalid) {
        this.lastNameMT = "error";
        if (this.lastName?.errors['required']) {
          this.lastNameIM = "Last name is required";
        }
        const validLastName = this.lastName.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.lastNameIM = '';
            this.lastNameMT = '';
            validLastName.unsubscribe();
          }
        });
      }
      if (this.address.invalid) {
        this.addressMT = "error";
        if (this.address?.errors['required']) {
          this.addressIM = "Address is required";
        }
        const validAddress = this.address.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.addressIM = '';
            this.addressMT = '';
            validAddress.unsubscribe();
          }
        });
      }
      if (this.email.invalid) {
        this.emailMT = "error";
        if (this.email?.errors['required']) {
          this.emailIM = "Email is required";
        }
        if (this.email?.errors['pattern']) {
          this.emailIM = "Invalid email";
        }
        const validEmail = this.email.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.emailIM = '';
            this.emailMT = '';
            validEmail.unsubscribe();
          }
        });
      }
      if (this.phoneNumber.invalid) {
        this.phoneNumberMT = "error";
        if (this.phoneNumber?.errors['required']) {
          this.phoneNumberIM = "Phone number is required";
        }
        if (this.phoneNumber?.errors['pattern']) {
          this.phoneNumberIM = "Invalid phone number";
        }
        if (this.phoneNumber?.errors['minlength']) {
          this.phoneNumberIM = "Phone Number length should be 10";
        }
        if (this.phoneNumber?.errors['maxlength']) {
          this.phoneNumberIM = "Phone Number length should be 10";
        }
        const validPhoneNumber = this.phoneNumber.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.phoneNumberIM = '';
            this.phoneNumberMT = '';
            validPhoneNumber.unsubscribe();
          }
        });
      }
      if (this.password.invalid) {
        this.passwordMT = "error";
        if (this.password?.errors['minlength']) {
          this.passwordIM = "Password length should be atleast 8-20 characters";
        }
        if (this.password?.errors['pattern']) {
          this.passwordIM = "Password length should be atleast 8-20 characters and 1-uppercase, 1-lowercase, 1-special character";
        }
        const validPassword = this.password.statusChanges.subscribe((status) => {
          if (status === 'VALID') {
            this.passwordIM = '';
            this.passwordMT = '';
            validPassword.unsubscribe();
          }
        });
      }
      if (this.orderLoginForm.errors?.['emailNotMatch']) {
        this.confirmEmailMT = "error";
        this.confirmEmailIM = "Email not match";
        const matchEmail = this.orderLoginForm.statusChanges.subscribe(() => {
          if (!this.orderLoginForm.errors?.['emailNotMatch']) {
            this.confirmEmailMT = '';
            this.confirmEmailIM = '';
            matchEmail.unsubscribe();
          }
        });
      }
      if (this.orderLoginForm.errors?.['passwordNotMatch']) {
        this.confirmPasswordMT = "error";
        this.confirmPasswordIM = "Password not match";
        const matchPassword = this.orderLoginForm.statusChanges.subscribe(() => {
          if (!this.orderLoginForm.errors?.['passwordNotMatch']) {
            this.confirmPasswordMT = '';
            this.confirmPasswordIM = '';
            matchPassword.unsubscribe();
          }
        });
      }
      return;
    }
    this.callRegisterCustomerApi();
  }

  callRegisterCustomerApi() {
    this.nextButtonDisable = true;
    this.nextButtonText = 'Processing Wait...';
    const registerCustomerApi = `${environment.api_url}Authenticate/register/customer?api-version=${environment.api_version}`;
    this.encryptedEmail = this.sharedService.getEncryptedValue(this.email.value);
    this.encryptedPassword = this.sharedService.getEncryptedValue(this.password.value ? this.password.value : this.authService.generateRandomPassword());
    let customerTaxType = this.residence.value ? CustomerTaxType.Residential : CustomerTaxType.Commercial;
    let taxExempt = this.taxExempt.value ? true : false;
    let payload = {
      "email": this.encryptedEmail,
      "password": this.encryptedPassword,
      "phoneNumber": this.phoneNumber.value,
      "firstName": this.firstName.value,
      "lastName": this.lastName.value,
      "deliveryStreet1": this.address.value,
      "deliveryCity": this.orderParams['city'],
      "deliveryCounty": this.orderParams['county'],
      "deliveryFips": this.orderParams['fips'],
      "deliveryState": this.orderParams['state'],
      "deliveryZIP": this.orderParams['zipCode'],
      "AutoPassword": this.password.value ? false : true,
      "oilTaxexempt": taxExempt,
      "deliveryStreet2": "",
      "customerTaxType": customerTaxType,
    };

    if (this.taxExempt.value) {
      payload['base64'] = this.base64.value;
    }

    this.callRegisterCustomerApiSubscription = this.http.post(registerCustomerApi, payload).subscribe({
      next: (res) => {
        let orderParamsUpdate = {
          ...this.orderParams,
          deliveryId: res['deliveryId']
        };
        this.storeService.addValueToStore('orderParams', orderParamsUpdate);
        this.nextButtonText = 'Redirecting...';
        this.loginCustomer();
      },
      error: (err) => {
        console.log(err)
        this.nextButtonDisable = false;
        this.nextButtonText = 'NEXT';
        if (err.error.errors) {
          err.error.errors.map((error) => {
            if (error['fieldName'] === "Email") {
              this.toastService.toastMessage.next({ title: 'Email is already taken', msg: error['errorMessage'], type: 'error' });
            }
          })
        }
      }
    })
  }

  loginCustomer() {
    const loginApiUrl = `${this.authService.apiUrl}authenticate/login?api-version=${this.authService.apiVersion}`;
    const payload = {
      "username": this.encryptedEmail,
      "password": this.encryptedPassword
    }

    this.loginApiSubscription = this.http.post(loginApiUrl, payload).subscribe({
      next: (res: LoginResponse) => {
        this.authService.isLoginDone.next({ isLoginDone: true, loginResponse: res });
        if (!this.appService.isApp) {
          if (!environment.is_local) {
            this.authService.processLoginResponse(res, '.heatfleet.com');
          } else {
            this.authService.processLoginResponse(res);
          }
        } else {
          const expirationDate = new Date(Date.now() + 7 * 24 * 60 * 60 * 1000); // Expires in 7 days
          const expires = expirationDate.toUTCString();
          this.authService.processLoginResponse(res, 'localhost', '/', expires);
        }
        this.router.navigate([URLS.CONFIRM_ADDRESS]);
      },
      error: (err) => {
        this.nextButtonDisable = false;
        this.nextButtonText = 'NEXT';
        console.log(err);
        this.authService.isLoginDone.next({ isLoginDone: false, loginResponse: null });
      }
    })
  }

  // Tax Exempt Modal
  openTaxExemptModel() {
    if (this.taxExempt.value) {
      this.fileUrl = undefined;
      this.showTaxExemptModel = true;
    }
  }

  closeTaxExemptModel(isClosed: boolean) {
    if (isClosed) {
      this.showTaxExemptModel = false;
      if (!this.fileUrl || this.fileUrl.split(',')[1] === '') {
        this.taxExempt.setValue(false);
      }
    }
  }

  onFileSelected(event: any) {
    const file: File = event.target.files[0];
    const allowTypes = [
      'image/png',
      'image/jpeg',
      'application/pdf',
      'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
    ];
    if (file && allowTypes.includes(file['type'])) {
      if (file['size'] > 0 && file['size'] <= 2000000) {
        this.convertToBase64(file);
      } else {
        this.toastService.toastMessage.next({ title: 'Size error', msg: 'file size should be below 2mb', type: 'error' });
      }
    } else {
      this.toastService.toastMessage.next({ title: 'This format is not supported', msg: '.pdf, .jpeg, .png, .docx are only supported format', type: 'error' });
    }
  }

  convertToBase64(file: File) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      this.fileUrl = reader.result as string;
    };
  }

  modalTaxExemptSubmit(fileUrl) {
    if (fileUrl && fileUrl.split(',')[1] && fileUrl.split(',')[1] !== '') {
      this.taxExempt.setValue(true);
      this.base64.setValue(this.fileUrl.split(',')[1]);
      this.showTaxExemptModel = false;
    } else {
      this.toastService.toastMessage.next({ title: 'Select a File', msg: 'Please select a file before submitting', type: 'error' });
    }
  }

  ngOnDestroy() {
    if (this.callRegisterCustomerApiSubscription) {
      this.callRegisterCustomerApiSubscription.unsubscribe();
    }
    if (this.emailInfo) {
      this.emailInfo.unsubscribe();
    }
    if (this.loginInfo) {
      this.loginInfo.unsubscribe();
    }
    if (this.loginApiSubscription) {
      this.loginApiSubscription.unsubscribe();
    }
  }
}
